import * as React from 'react';
import Box from '@mui/material/Box';
import Tooltip from '@mui/material/Tooltip';
import AddIcon from '@mui/icons-material/Add';
import EditIcon from '@mui/icons-material/Edit';
import DeleteIcon from '@mui/icons-material/DeleteOutlined';
import SaveIcon from '@mui/icons-material/Save';
import CancelIcon from '@mui/icons-material/Close';
import {
  GridRowModes,
  DataGrid,
  GridActionsCellItem,
  GridRowEditStopReasons,
  Toolbar,
  ToolbarButton,
} from '@mui/x-data-grid';

import { useEffect, useState, useRef } from 'react';
import { useAuth } from '../contexts/AuthContext';

//other dependencies that we need:
// @emotion/react
// @emotion/styled
// @mui/styled-engine

//npm i @mui/material @mui/icons-material @emotion/react @emotion/styled @mui/styled-engine @mui/x-data-grid
//test



function EditToolbar(props) {
  const { setRows, setRowModesModel, resetPage, isEditing } = props;

  const handleClick = () => {
    if (isEditing())
      return;
    const id = 0;
    resetPage();
    setRows((oldRows) => [
      { id: id, key: '', description: '', isPartial: false, isNew: true },
      ...oldRows     
    ]);
    setRowModesModel((oldModel) => ({  
      [id]: { mode: GridRowModes.Edit, fieldToFocus: 'key' },
      ...oldModel,
    }));
  };

  return (
    <Toolbar>
      <Tooltip title="Add record">
        <ToolbarButton onClick={handleClick}>
          <AddIcon fontSize="small" />
        </ToolbarButton>
      </Tooltip>
    </Toolbar>
  );
}

export default function UserAgents(props) {
  //const [rows, setRows] = React.useState(initialRows);
  const [rows, setRows] = useState([]);
  const [rowModesModel, setRowModesModel] = useState({});
  const [pageSize, setPageSize] = useState(10);
  const [paginationModel, setPaginationModel] = useState({ page: 0, pageSize: 10 });
  const [filterModel, setFilterModel] = useState({ items: [] }); // Initial state: no filters applied
  const [sortModel, setSortModel] = useState([]);
  const apiRef = useRef(null);

  // Use authenticated fetch from auth context
  const { authenticatedFetch } = useAuth();
  const baseApiUrl = import.meta.env.VITE_API_BASE_URL;
  const userAgentUrl = `${baseApiUrl}/v1/api/UserAgent`;
  
  console.log("User Agent URL: " + userAgentUrl);

  const saveRecord = (userAgent) => {
        let method;
        let url;
        const id = userAgent.id;
        if (id > 0){
          method = "PUT";
          url = `${userAgentUrl}/${id}`;
        } else {
          method = "POST";
          url = userAgentUrl;
        }
        authenticatedFetch(url, {
        method: method,
        headers: {
          'Content-Type': 'application/json'
        },
        body: JSON.stringify(userAgent)
      })
      .then(response => {
          if (!response.ok) {
            throw new Error(`HTTP error! Status: ${response.status}`);
          }
          return response.text();
        })
        .then(data => {
          console.log('Successful saved record. Data:', data);
          var newUserAgent = JSON.parse(data);
          // Handle the data
          setRows(rows.map((row) => (row.id === userAgent.id ? newUserAgent : row)));
          userAgent.id = newUserAgent.id;
        })
        .catch(error => {
          console.error('Error Saving id ' + id + ':', error);
          // Handle errors
        });
  }

  const deleteRecord = (id) => {
        authenticatedFetch(userAgentUrl + `/${id}`, {
        method: 'DELETE',
        headers: {
          'Content-Type': 'application/json'
        },
      })
      .then(response => {
          if (!response.ok) {
            throw new Error(`HTTP error! Status: ${response.status}`);
          }
          return response.text();
        })
        .then(data => {
          console.log('Data:', data);
          // Handle the data
          setRows(rows.filter((row) => row.id !== id));
        })
        .catch(error => {
          console.error('Error Deleting id ' + id + ':', error);
          // Handle errors
        });
  }

  const getRecords = () => {
      console.log("Fetching data");
      authenticatedFetch(userAgentUrl + '?skip=0&top=0&descending=false', {
        method: 'GET',
        headers: {
          'Content-Type': 'application/json'
        },
      })
      .then(response => {
          if (!response.ok) {
            throw new Error(`HTTP error! Status: ${response.status}`);
          }
          return response.json();
        })
        .then(data => {
          console.log('Data:', data);
          // Handle the data
          setRows(data);
        })
        .catch(error => {
          console.error('Error fetching data:', error);
          // Handle errors
        });
  }


    useEffect(() => {
        getRecords();

      }, []
    );


  const resetPage = () => {
    setFilterModel({ items: [] });
    setSortModel([]);
    setPaginationModel({
      ...paginationModel,
      page: 0,
    });
  };

  const isEditing = () =>{
    return (Object.keys(rowModesModel).length > 0);
    //return false;
  }

  const handleRowEditStart = (params, event) => {
    // If a row is already being edited, prevent starting a new edit
    if (isEditing()) {
      event.defaultMuiPrevented = true; // Prevent default behavior
      return;
    }

    setRowModesModel({ ...rowModesModel, [params.id]: { mode: GridRowModes.Edit } });
  }

  const handleRowEditStop = (params, event) => {
    if (params.reason === GridRowEditStopReasons.rowFocusOut) {
      event.defaultMuiPrevented = true;
    }
  };

  const handleEditClick = (id) => () => {
    // If a row is already being edited, prevent starting a new edit
    if (isEditing()) {
      return;
    }
    setRowModesModel({ ...rowModesModel, [id]: { mode: GridRowModes.Edit } });
  };

  const handleSaveClick = (id) => () => {
    //var matchingRow = rows.find((row) => row.id === id);
    var editRows = apiRef.current.state.editRows;
    var matchingRow = editRows[id];
    if (!matchingRow.key || matchingRow.key.value == ""){
      window.alert("User agent cannot be blank");
      return;
    }
    setRowModesModel({ ...rowModesModel, [id]: { mode: GridRowModes.View } });
  };

  // const handleProcessRowUpdate = (updatedRow, originalRow) => {
  //   // Send the updated row data to your server for saving
  //   saveRecord(updatedRow);

  //   // Return the updated row to update the DataGrid's internal state
  //   return updatedRow;
  // };

  const handleDeleteClick = (id) => () => {
    if (window.confirm("Are you sure you want to delete item " + id + "?")) {
      //setRows(rows.filter((row) => row.id !== id));
      deleteRecord(id);
    }
  };

  const handleCancelClick = (id) => () => {
    setRowModesModel({
      ...rowModesModel,
      [id]: { mode: GridRowModes.View, ignoreModifications: true },
    });

    const editedRow = rows.find((row) => row.id === id);
    if (editedRow.isNew) {
      setRows(rows.filter((row) => row.id !== id));
    }
  };

  const processRowUpdate = (newRow) => {
    const updatedRow = { ...newRow, isNew: false };
    saveRecord(updatedRow);
    //setRows(rows.map((row) => (row.id === newRow.id ? updatedRow : row)));
    return updatedRow;
  };

  const handleRowModesModelChange = (newRowModesModel) => {
    setRowModesModel(newRowModesModel);
  };


  const columns = [
    { field: 'id', headerName: 'ID', width: 180, editable: false },
    {
      field: 'key',
      headerName: 'User Agent',
      type: 'string',
      width: 300,
      align: 'left',
      headerAlign: 'left',
      editable: true,
    },
    {
      field: 'description',
      headerName: 'Description',
      type: 'string',
      width: 300,
      align: 'left',
      headerAlign: 'left',
      editable: true,
    },
    {
      field: 'isPartial',
      headerName: 'Is Partial',
      type: 'boolean',
      width: 100,
      editable: true,
    },
    {
      field: 'actions',
      type: 'actions',
      headerName: 'Actions',
      width: 100,
      cellClassName: 'actions',
      getActions: ({ id }) => {
        const isInEditMode = rowModesModel[id]?.mode === GridRowModes.Edit;

        if (isInEditMode) {
          return [
            <GridActionsCellItem
              icon={<SaveIcon />}
              label="Save"
              material={{
                sx: {
                  color: 'primary.main',
                },
              }}
              onClick={handleSaveClick(id)}
            />,
            <GridActionsCellItem
              icon={<CancelIcon />}
              label="Cancel"
              className="textPrimary"
              onClick={handleCancelClick(id)}
              color="inherit"
            />,
          ];
        }

        return [
          <GridActionsCellItem
            icon={<EditIcon />}
            label="Edit"
            className="textPrimary"
            onClick={handleEditClick(id)}
            color="inherit"
          />,
          <GridActionsCellItem
            icon={<DeleteIcon />}
            label="Delete"
            onClick={handleDeleteClick(id)}
            color="inherit"
          />,
        ];
      },
    },
  ];

  return (
    <Box
      sx={{
        height: '100%',
        width: '100%',
        '& .actions': {
          color: 'text.secondary',
        },
        '& .textPrimary': {
          color: 'text.primary',
        },
      }}
    >
      <DataGrid
        apiRef={apiRef}
        filterModel={filterModel}
        onFilterModelChange={(newModel) => setFilterModel(newModel)}
        sortModel={sortModel}
        onSortModelChange={(newModel)=> setSortModel(newModel)}
        pageSize={pageSize}
        pageSizeOptions={[5, 10, 15, 25, 100]}
        onChangePageSize={(newPageSize) => setPageSize(newPageSize)}
        pagination
        // initialState={{
        //   pagination: {
        //     paginationModel: { pageSize: 10, page: 0 },
        //   },
        // }}
        paginationModel={paginationModel}
        onPaginationModelChange={(newModel) => setPaginationModel(newModel)}
        rows={rows}
        columns={columns}
        editMode="row"
        rowModesModel={rowModesModel}
        onRowModesModelChange={handleRowModesModelChange}
        onRowEditStop={handleRowEditStop}
        onRowEditStart={handleRowEditStart}
        processRowUpdate={processRowUpdate}
        slots={{ toolbar: EditToolbar }}
        slotProps={{
          toolbar: { setRows, setRowModesModel, resetPage, isEditing },
        }}
        showToolbar
      />
    </Box>
  );
}
